.navbar {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 45px;
  background-color: #f1f1f1;
  padding: 10px 50px;
  /* makes navbar appear above li elements in tiles and dropdowns */
  z-index: 2;
}

.navbar a {
  float: left;
  color: black;
  text-align: center;
  padding: 5px 10px;
  margin: 5px 10px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

.navbar a:hover {
  background-color: #ddd;
  cursor: pointer;
}

.navbar-right {
  float: right;
}

.logo {
  position: absolute;
  top: 5px;
  height: 55px;
  border-radius: 50%;
}

.logo:hover {
  cursor: pointer;
}

/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */
@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}
